import React from 'react'
import Logo from "../img/logo.png"

const Navbar =()=> {
    return (
        <>
          <nav className="navbar">
              <div className="navbar_text">
                  <div className="logo">
                    <img src={Logo}  alt="logo"/>
                  </div>
                    <ul className="navbar_ul">
                        <li><a to="">Home</a></li>
                        <li><a to="">Menu</a></li>
                        <li><a to="">Shop</a></li>
                        <li><a to="">News</a></li>
                        <li><a to="">Contact</a></li>
                    </ul>
              </div>
            </nav>  
        </>
    )
}

export default Navbar
